import * as util from '@/utils';
import simulate from '@/common/simulate';
import { $$set, $$get } from '@/common/global';
import parseQrCode from './code';
import getDeviceData from './device';
import { accreditUserInfo, accreditUserLocation, getLocation } from './accredit.js';

export default {
	data() {
		return {
			hasAdvert: false, //默认显示广告
			hasUserAccredit: true, //默认已授权
			userAccreditStyle: '', //授权点击样式

			advertTime: 5,
			advertImgUrls: []
		};
	},
	onLoad(options) {
		//通过退出按钮切换的
		if (options && options.pageMode === 'logout') {
			// this.startApp();
			return;
		}

		//解析二维码/转发等
		parseQrCode(options);

		//权限
		accreditUserInfo(
			data => {
				$$set.userInfo(data);
				//定位服务
				accreditUserLocation(() => {
					getLocation(() => {
						this.$api.showBusy();
						this.initDeviceData(() => {
							// 固码支付
							if ($$get.share('type') === 'qrPay') {
								//util.gotoPage('/common/qr-pay/qr-pay');
							} else {
								this.enterUrl();
							}
						});
					});
				});
			},
			e => {
				//未授权
				this.hasUserAccredit = false;
			}
		);
	},
	methods: {
		/**
		 * 进入
		 */
		enterUrl() {
			//跳过广告
			let testUrl = simulate();
			if (testUrl) {
				this.$api.gotoPage('reLaunch', testUrl);
				return;
			}

			//如果是转发进来的,直接进入
			if ($$get.share('type') === 'goods') {
				this.$api.hideBusy();
				this.$api.gotoPage('reLaunch', `/common/goods/goods?agentidsp=${app.globalData.share.agentidsp}&goodsid=${app.globalData.share.goodsid}`);
				return;
			}

			//分享注册
			if ($$get.share('type') == 'register' && !app.$$hasLogin()) {
				this.$api.hideBusy();
				this.$api.gotoPage('reLaunch', `/common/login/login?pageType=user`);
				return;
			}

			//广告只显示一次
			this.runAdvert();
		},

		/**
		 * 销毁定时器
		 */
		countDownDestory() {
			if (this.countDown) {
				this.countDown.destory();
				this.countDown = null;
			}
			this.autoplay = false;
		},

		/**
		 * 登录广告
		 */
		runAdvert() {
			util.getAdvert().then(response => {
				const images = [];
				response.data.list.map(item => {
					if (Math.random() * 10 > 5) {
						images.push(`${item.advUrl}${item.advImg}`);
					} else {
						images.unshift(`${item.advUrl}${item.advImg}`);
					}
				});
				if (images.length) {
					this.$api.hideBusy();
					this.hasAdvert = true;
					this.advertImgUrls = images;
					this.countDown = new util.CountDown({
						checkTime: 5
					});
					this.countDown.$$watch('init', newTime => {
						this.advertTime = 5;
					});
					this.countDown.$$watch('change', newTime => {
						this.advertTime = newTime;
					});
					this.countDown.$$watch('end', defaultTime => {
						this.countDownDestory();
						this.startApp();
					});
					this.countDown.start();
				}
			});
		},

		/**
		 * 进入
		 */
		startApp() {
			this.$api.hideBusy();

			//隐藏广告
			this.hasAdvert = false;

			//如果是分享进来,直接个人中心
			this.$api.gotoPage('reLaunch', '/pages/tabbar/homepage/homepage');
		},

		//==========================================

		/**
		 * 初始化设备数据
		 */
		initDeviceData(callback) {
			getDeviceData().then(() => {
				this.$api
					.accessLogin()
					.then(callback)
					.catch(callback);
			});
		},

		/**
		 * 开始页面操作
		 */
		nextProcess() {},

		/**
		 * 获取用户授权数据
		 */
		onGetUserAccredit() {
			util
				.getUserInfo()
				.then(data => {
					$$set.userInfo(data);
					this.hasUserAccredit = true;
					this.nextProcess();
				})
				.catch(() => {
					this.userAccreditStyle = false;
					this.$api.showModal({
						title: '温馨提示',
						showCancel: false,
						confirmText: '继续授权',
						content: '亲！拒绝将无法进入哦！您确定这么做吗'
					});
				});
		},

		/**
		 * 如果点击了授权
		 */
		onChangeUserAccreditButton() {
			this.userAccreditStyle = true;
		}
	}
};
